*{
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
}
body{
  background-color: #34495E;
}
.heading{
  width: 100vw;
  height: 20vh;
  text-align: center;
  font-size: 3rem;
  padding-top: 1rem;
  color: white;
  font-family: monospace;
  font-weight: bolder;
}
.middle{
  width: 100vw;
  height: 80vh;
  display: flex;
  align-items: flex-start;
  justify-content: center;
}
.container{
  width: 50%;
  height: 90%;
  display: grid;
  grid-template-rows: 25% 70%;
  background-color: rgb(127, 195, 195);
  border: solid black 3px;
  border-radius: 0.5rem;
  padding: 1rem;
  gap: 1rem;

}

.input{
  display: flex;
  align-items: center;
  justify-content: center;
}
.input input{
  width: 100%;
  height: 90%;
  padding: 0.8rem;
  font-size: 3rem;
  text-align: right;
  border-radius: 0.5rem;
  transition: 0.5s ease-in-out;

}
.buttons{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
.row{
  display:flex;
  padding: 0.5rem;
  width: 100%;
  height: 16%;
  align-items: center;
  justify-content: space-evenly
}

.btn,.acbtn{
  background-color: rgb(241, 247, 247);
  width: 20%;
  height: 100%;
  border-radius: 0.5rem;
  display: flex;
  justify-content: center;
  font-size: 2rem;
  font-weight: 200; 
  align-items: center;

}

.btn:hover,.acbtn:hover{
  background-color: rgb(212, 225, 225);;
}


.eqbtn{
  background-color: blue;
  color: white;
  width: 20%;
  height: 100%;
  border-radius: 0.5rem;
  display: flex;
  justify-content: center;
  font-size: 2rem;
  font-weight: 200; 
  align-items: center;
  transition: 0.3s ease-in-out;

}

.eqbtn:hover{
  background-color:white;
  color: blue;;

}

@media(max-width:702px){
  .container{
    width: 80%;
  }
}

